@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap);
body{
        background: #000 url(/shopiffyChallenge/static/media/stars.90c266f7.png) repeat top center;
        z-index:0;
}
.header {
    width: 100%;
    height: 80px;
    color: white;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2%;
  }
  .header__img {
    width: 120px;
    margin-top: 20px;
    margin-left: 7%;

    -webkit-animation: 2s navbar 0.5s ease-in-out;

            animation: 2s navbar 0.5s ease-in-out;
  }
  @-webkit-keyframes navbar {
    0%{
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
    100%{
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }
  @keyframes navbar {
    0%{
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
    100%{
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
  }
  

  
.heart{
    position: relative;
    height: 20px;
    width: 20px;
    background-color: #c9c9c9;
    justify-content: center;
    border-radius: 0 0 0 10px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    cursor: pointer;

  }
  .heart.fill-color{
    background-color:#ff2727;
  }
  .heart::before{
    position: absolute;
    content: '';
    top: -50%;
    left: 0;
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: 50%;
  }
  .heart:after{
    position: absolute;
    content: '';
    top: 0;
    right: -50%;
    width: 100%;
    height: 100%;
    background: inherit;
    border-radius: 50%;    

  }
  .animation-heart{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    background: #ff2727;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    border-radius: 0 0 0 10px;
    cursor: pointer;
    visibility: visible;
  }

  /* .animation-heart.animation{
      animation: popup .8s linear;
      visibility: visible;
  } */

  .animation-heart:after{
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: -50%;
    right: 0;
    background: inherit;
    border-radius: 50%;
  }
  .animation-heart::before{
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0;
    right: -50%;
    background: inherit;
    border-radius: 50%;
  }

  @-webkit-keyframes popup {
    100%{
      -webkit-transform: scale(1.2)rotate(-45deg);
              transform: scale(1.2)rotate(-45deg);
      opacity: 0;
    }
  }

  @keyframes popup {
    100%{
      -webkit-transform: scale(1.2)rotate(-45deg);
              transform: scale(1.2)rotate(-45deg);
      opacity: 0;
    }
  }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
  }

.container{
    display: grid;
    min-height: 100vh;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    justify-items: center;
    margin-left: 4%;
}

.card {
  position: relative;
  min-width: 320px;
  height: 440px;
  box-shadow: 
  inset 5px 5px 5px rgb(0 0 0 / 20%),
  inset -5px -5px 15px rgb(255 255 255 / 24%),
  5px 5px 15px rgb(0 0 0), -5px -5px 15px rgb(255 255 255 / 34%);
  border-radius: 15px;
  margin: 30px;
  transition: 0.5s;
  -webkit-animation: 2s card 0.5s ease-in-out;
          animation: 2s card 0.5s ease-in-out;
}
@-webkit-keyframes card {
  0%{
    -webkit-transform: translateX(1200px);
            transform: translateX(1200px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes card {
  0%{
    -webkit-transform: translateX(1200px);
            transform: translateX(1200px);
  }
  100%{
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

.container .card:nth-child(1) .box .content a {
    background: #2196f3;
  }

.box {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background: #2a2b2f;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: 0.5s;
  }
  .box:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background: rgba(255, 255, 255, 0.03);
  }
  .content {
    
    text-align: center;
  }
  .content img {
      margin-top: 25%;
      width: 220px;
      height: 150px;
  }
  .content h2 {
    position: absolute;
    top: -25px;
    right: 2px;
    font-size: 6rem;
    color: rgba(255, 255, 255, 0.2);
  }
  .content h4 {
    font-weight: 500;
    color: rgba(255, 255, 255, 0.9);
  }
  .content p {
    font-size: 1rem;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.9);
    z-index: 1;
    transition: 0.5s;
  }
  
  .button{
    margin-top: 5%;
    display: flex;
    align-self: center;
    justify-self: center;
  }

  @media(max-width: 700px){
    .container{
      grid-template-columns: repeat(2, 1fr);
    } 
  }
  @media (max-width: 400px) {
    .container{
      grid-template-columns: repeat(1, 1fr);
    }
  }
.load-button{
    border-style: none;
    color: whitesmoke;
    background-color: #ff7575;
    width: 150px;
    height: 60px;
    border-radius: 5px;
}
* {
  margin: 0;
  padding: 0;
}
.title h1{
  font-size: 36px;
  font-family: 'Lato', sans-serif;
  margin-top:10em;
  margin-left: auto;
  margin-right:auto;
  text-align: center;

  /* Stryling the text and giving it gradiant */
  color:#ffffff;
  background: -webkit-linear-gradient(#eee, #333);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 6px 6px 0px rgba(0,0,0,0.2);

  /* Make sure text is infront of background images */
  display:block;
  position: relative;
  z-index: 3;
}

@-webkit-keyframes move-twink-back {
  from {background-position:0 0;}
  to {background-position:-10000px 5000px;}
}

@keyframes move-twink-back {
  from {background-position:0 0;}
  to {background-position:-10000px 5000px;}
}

@-webkit-keyframes move-clouds-back {
  from {background-position:0 0;}
  to {background-position:10000px 0;}
}

@keyframes move-clouds-back {
  from {background-position:0 0;}
  to {background-position:10000px 0;}
}

.stars, .twinkling, .clouds {
position:absolute;
top:0;
left:0;
right:0;
bottom:0;
width:100%;
height:100%;
display:block;
}

.stars {
background:#000 url(/shopiffyChallenge/static/media/stars.90c266f7.png) repeat top center;
z-index:0;
}

.twinkling{
background:transparent url(data:image/png;base64,) repeat top center;
z-index:1;
-webkit-animation:move-twink-back 200s linear infinite;
        animation:move-twink-back 200s linear infinite;
}

.clouds{
  background:transparent url(/shopiffyChallenge/static/media/clouds.655c9c06.png) repeat top center;
  z-index:2;
  opacity: .4;
  -webkit-animation:move-clouds-back 200s linear infinite;
          animation:move-clouds-back 200s linear infinite;
}

